*{box-sizing: border-box;}
body {
  width: 100%;
  height: 100vh; /* viewport height */
  display:flex;
  justify-content: center;
  align-items: center;
  background-color: #ffd449;
}

.btn{
  background-color: white;
  color: #548c2f;
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  margin: 15px;
  padding: 15px;
  border: 2px solid #548c2f;
  border-radius: 5px;
  box-shadow: 0 12px #548c2f, 0 18px rgba(0, 0, 0, 0.4);
  font-size: 60px;
  width: 90px;
  height: 80px;
  cursor: pointer;
  position: relative;
  top: 0;
}

.btn:hover {
  background-color: #548c2f;
  color: #ffd449;
  top: 4px;
  box-shadow: 0 8px #548c2f, 0 14px rgba(0, 0, 0, 0.4);
}

.btn:active {
  top: 8px;
  box-shadow: 0 4px #548c2f, 0 10px rgba(0, 0, 0, 0.4);
}

.game{
  display:flex;
  flex-wrap: wrap;
}

.game-info{
  margin: 15px;
  color: white;
}

.board > div {
  display: flex;
}